import { useState, useEffect } from "react";
import {
	BrowserRouter as Router,
	Switch,
	Route,
  } from "react-router-dom";
import { ChakraProvider, Flex, Text, Image, Spinner } from "@chakra-ui/react";
import "./App.scss";
import "./mdi/css/materialdesignicons.css";
import axios from "axios";
import Toast from "./toast";
const deflink = process.env.REACT_APP_API_BASE_URL


function App() {
	const [loading, setLoading] = useState("")
	const [kycHolder, setKycHolder] = useState(false)
	const [allUsers, setAllUsers] = useState(false)
	async function request(type, url, data) {
		//AXIOS CLIENT HEADER
		const client = axios.create({
		  baseURL: deflink,
		  headers: {
			"Content-Type": "multipart/form-data",
		  },
		});
		try {
		  const response = type === "post" ? await client.post(url, data) : await client.get(url);
		  return response?.data;
		}
		catch (error) {
			if (error?.response?.status) {
				return error?.response?.data?.message;
			}
			else {
				return "Cannot connect at the moment, please try again";
			}
		}
	}

	async function link(data, id) {
        setLoading(id)
        const res = await request("post", "misc.php", data)
        if(res?.status) {
            Toast(res?.message, "success")
			if(id === "btn6") {
				setKycHolder([res?.data])
			}
			if(id === "btn7") {
				setAllUsers([res?.data])
			}
			if(id === "btn8") {
				setAllUsers([res?.data])
			}
        }
        else {
            Toast(res?.message, "error")
        }
        setLoading("")
    }

	function myFunc(e, id) {
		e.preventDefault()
		const data = new FormData()
		data.append("id", id)
		if(id === "btn1") {
			data.append("email", document.getElementById('email1').value)
			data.append("amount", document.getElementById('amount1').value)
			link(data, id)
		}
		else if(id === "btn2") {
			data.append("email", document.getElementById('email2').value)
			data.append("amount", document.getElementById('amount2').value)
			link(data, id)
		}
		else if(id === "btn3") {
			data.append("email", document.getElementById('email3').value)
			link(data, id)
		}
		else if(id === "btn4") {
			data.append("email", document.getElementById('email4').value)
			data.append("amount", document.getElementById('amount4').value)
			data.append("method", document.getElementById('method').value)
			data.append("acc", document.getElementById('acc').value)
			data.append("accname", document.getElementById('accname').value)
			data.append("transdate", document.getElementById('transdate').value)
			data.append("transstatus", document.getElementById('transstatus').value)
			link(data, id)
		}
		else if(id === "btn5") {
			data.append("email", document.getElementById('email5').value)
			data.append("amount", document.getElementById('amount5').value)
			link(data, id)
		}
		else if(id === "btn6") {
			setKycHolder(false)
			data.append("email", document.getElementById('email6').value)
			link(data, id)
		}
		else if(id === "btn7") {
			setAllUsers(false)
			link(data, id)
		}
		else if(id === "btn8") {
			setAllUsers(false)
			link(data, id)
		}
	}

	return (
		<ChakraProvider>
			<Flex w="100%" justify="space-between" flexWrap="wrap" color="#fff" py="5%" px="10%">
				<Flex direction="column" w="30%" mb="12">
					<Text mb="3">Add Deposit</Text>
					<form onSubmit={(e) => myFunc(e, "btn1")}>
						<input type="email" required placeholder="customer email" id="email1" />
						<br/><br/>
						<input type="number" required placeholder="amount" id="amount1" />
						<br/><br/>
						<button id="btn1" type="submit">{loading === "btn1" ? <Spinner /> : "Save"}</button>
					</form>
				</Flex>

				<Flex direction="column" w="30%" mb="12">
					<Text mb="3">Add Referral balance</Text>
					<form onSubmit={(e) => myFunc(e, "btn2")}>
						<input type="email" required placeholder="customer email" id="email2" />
						<br/><br/>
						<input type="number" required placeholder="amount" id="amount2" />
						<br/><br/>
						<button id="btn2" type="submit">{loading === "btn2" ? <Spinner /> : "Save"}</button>
					</form>
				</Flex>


				<Flex direction="column" w="30%" mb="12">
					<Text mb="3">Delete User</Text>
					<form onSubmit={(e) => myFunc(e, "btn3")}>
						<input type="email" required placeholder="customer email" id="email3" />
						<br/><br/>
						<button id="btn3" type="submit">{loading === "btn3" ? <Spinner /> : "Save"}</button>
					</form>
				</Flex>

				<Flex direction="column" w="30%" mb="12">
					<Text mb="3">Fill deposit ticket</Text>
					<form onSubmit={(e) => myFunc(e, "btn4")}>
						<input type="email" required placeholder="customer email" id="email4" />
						<br/><br/>
						<input type="number" required placeholder="amount" id="amount4" />
						<br/><br/>
						<input type="text" required placeholder="method (payeer, btc e.t.c)" id="method" />
						<br/><br/>
						<input type="text" required placeholder="account no or wallet address" id="acc" />
						<br/><br/>
						<input type="text" required placeholder="account name or wallet name" id="accname" />
						<br/><br/>
						<input type="text" required placeholder="date (format dd-mm-yyyy)" id="transdate" />
						<br/><br/>
						<input type="text" required placeholder="Status (approved or pending)" id="transstatus" />
						<br/><br/>
						<button id="btn4" type="submit">{loading === "btn4" ? <Spinner /> : "Save"}</button>
					</form>
				</Flex>

				<Flex direction="column" w="30%" mb="12">
					<Text mb="1">Verify Kyc</Text>
					<Text mb="3">NOTE: 0 is for pending, 1 is for verified and 2 is for rejected</Text>
					<form onSubmit={(e) => myFunc(e, "btn5")}>
						<input type="email" required placeholder="customer email" id="email5" />
						<br/><br/>
						<input type="number" required placeholder="verify number (0, 1 or 2)" id="amount5" />
						<br/><br/>
						<button id="btn5" type="submit">{loading === "btn5" ? <Spinner /> : "Save"}</button>
					</form>
				</Flex>

				<Flex direction="column" w="30%" mb="12">
					<Text mb="3">Get KYC files</Text>
					<form onSubmit={(e) => myFunc(e, "btn6")}>
						<input type="email" required placeholder="customer email" id="email6" />
						<br/><br/>
						<button id="btn6" type="submit">{loading === "btn6" ? <Spinner /> : "Save"}</button>
					</form>
					{
						kycHolder !== false &&
						<Flex w="100%" mt="12" direction="column">
							<Text mb="2">Selfie</Text>
							<Image src={kycHolder[0]?.selfie} w="100%" mb="4" />
							
							<Text mb="2">Card Front</Text>
							<Image src={kycHolder[0]?.cardfront} w="100%" mb="4" />
							
							<Text mb="2">Card Back</Text>
							<Image src={kycHolder[0]?.cardback} w="100%" mb="4" />

							<a href={kycHolder[0]?.proofaddress} target="_BLANK" style={{ color: "aliceblue" }}>Proof of address</a>
						</Flex>
					}
				</Flex>
				
				<Flex direction="column" w="100%" mt="12">	
					<Flex w="50%" justify="space-between">	
						<form onSubmit={(e) => myFunc(e, "btn7")}>
							<button id="btn7" type="submit">{loading === "btn7" ? <Spinner /> : "Show all users with KYC"}</button>
						</form>
						
						<form onSubmit={(e) => myFunc(e, "btn8")}>
							<button id="btn8" type="submit">{loading === "btn8" ? <Spinner /> : "Show all users generally"}</button>
						</form>
					</Flex>
					{
						allUsers !== false &&
						<Flex w="50%" mt="12" direction="column">
							{
								allUsers[0]?.map((item, index) => (
									<Flex key={index} mb="3">
										<Text mr="4" w="10%">{index + 1}.</Text>
										<Text mr="4" w="70%" cursor="pointer" onClick={() => {
											navigator.clipboard.writeText(item?.email)
											Toast("Copied to clipboard", "success")
										}}>{item?.email}</Text>
										<Text mr="4" w="20%" color={item.kyc === '0' ? "#e6b217" : item.kyc === '1' ? "#33e88b" : item.kyc === '2' ? "#e01433" : "#fff"}>{item.kyc === '0' ? "Pending" : item.kyc === '1' ? "Verified" : item.kyc === '2' ? "Rejected" : ""}</Text>
									</Flex>
								))
							}
						</Flex>
					}
				</Flex>
				
			</Flex>
		</ChakraProvider>
	)
}


export default App;